export const productSchema = props => {
  const schema = {
    '@context': 'https://www.schema.org',
    '@type': 'product',
    brand: props.seller,
    name: props.name,
    image: props.image,
    url: props.url,
  };
  if (props.ratingAverage) {
    schema.aggregateRating = {
      '@type': 'aggregateRating',
      ratingValue: props.ratingAverage,
      reviewCount: props.ratingQuantity,
    };
  }
  if (props.price) {
    schema.offers = {
      '@type': 'Offer',
      priceCurrency: 'BRL',
      price: props.price,
      itemCondition: props.conditions && props.conditions.schema
        ? props.conditions.schema
        : 'https://schema.org/NewCondition',
      availability: props.inStock ? 'https://schema.org/InStock' : 'https://schema.org/OutOfStock',
      seller: {
        '@type': 'Organization',
        name: props.seller,
      },
    };
  }
  return JSON.stringify(schema);
};

export const breadcrumbSchema = navigation => {
  const list = navigation.map((item, index) => {
    if (index === 0) {
      return {
        '@type': 'ListItem',
        position: 1,
        item: {
          '@id': item.url,
          name: 'Pgina Inicial',
        },
      };
    } else {
      return {
        '@type': 'ListItem',
        position: index + 1,
        item: {
          '@id': item.url,
          name: item.name,
        },
      };
    }
  });

  const schema = {
    '@context': 'https://www.schema.org',
    '@type': 'BreadcrumbList',
    itemListElement: list,
  };

  return JSON.stringify(schema);
};
