export const warningDeprecatedProps = (component, message) => {
  if (process.env.NODE_ENV != 'production') {
    const warningMessage = `WARNING CROSS-THEME [${component}]: ${message}. Esta mudana valer a partir do dia 1 de Novembro de 2018. Lembrem-se de refatorar at l!`;

    if (typeof console !== 'undefined') {
      console.warn(warningMessage);
    }
  }
};
